@media screen and (max-width: 800px) {
  .welcome__container {
    display: flex;
    flex-direction: column;
    width: 100%;
    justify-content: center;
    align-items: center;
  }

  .welcome__wrapper {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    position: absolute;
  }
  video {
    width: 100%;
    height: 90vh;
    object-fit: cover;
  }
  .welcome__wrapper h1 {
    color: rgb(233, 233, 233);
    font-family: system-ui, -apple-system, BlinkMacSystemFont, "Segoe UI",
      Roboto, Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue",
      sans-serif;
    font-size: 2rem;
    font-weight: 700;
    text-shadow: -2px -2px 10px rgb(136, 136, 136),
      2px -2px 10px rgb(78, 78, 78);
    text-align: center;
    margin: 0px 0px;
    user-select: none;
  }
  .welcome__wrapper h2 {
    color: rgb(233, 233, 233);
    font-family: system-ui, -apple-system, BlinkMacSystemFont, "Segoe UI",
      Roboto, Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue",
      sans-serif;
    font-size: 1rem;
    font-weight: 500;
    text-align: center;
    margin: 50px 5px;
    user-select: none;
  }
  .welcome__container h3 {
    align-self: flex-end;
    color: rgb(51, 51, 51);
    margin: 0;
    font-size: 9px;
    user-select: none;
  }
}
